* {
	margin: 0;
	padding: 0;
}
html,
body {
	height: 100%;
}
#root {
	display: flex;
	flex-direction: column;
	min-height: 100%;
}
.content {
	flex: 1 0 auto;
	height: 100%;
}
.footer {
	flex: 0 0 auto;
}

a {
	text-decoration: none;
	color: inherit;
	transition: 0.2s;
}

a:hover{
	color: black;
}

.active {
	color: black;
}
